
.container {
    display: -ms-flexbox;
    display: flex;
    min-height: 80%;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 20px;
    padding-bottom: 50px;

    margin: 0 auto;
    z-index: -1;
  }
  
  @media (min-width: 1100px) {
    .container {
      padding: 9em 0;
      max-width: 68rem;
    }
  }


